@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{color:#f8f8f2;background-color:#282a36}.container{display:flex;align-items:center;flex-direction:column}.content{display:flex;flex-direction:column;align-items:center;justify-content:space-between}.searchContainer{width:100%;min-width:50em;display:flex;margin-bottom:50px}.searchContainer input{border:none;width:100%;min-width:50em;font-size:20px;padding:0.4em}.searchContainer button{border:none;background-color:#bd93f9;color:white;padding:0.5em;font-size:20px;width:10em;font-weight:bold}.searchContainer button:hover{opacity:0.4}.results{display:flex;flex-direction:column;width:100%}

.card{background-color:#44475a;padding:1em;margin-bottom:1em;max-width:50em}.card div{display:flex;align-items:center;justify-items:center;font-size:20px;padding:0.4em}.card div svg{margin-right:0.3em}

*{font-family:"Roboto";font-size:30px}body{margin:0}

